@import "../../../styles/_variables.module.scss";

.weather {
  @include flex_col;
  align-items: center;
  width: 31%;
  height: 100%;
  background-color: white;
  box-shadow: 1px 2px 6px 2px rgba(56, 56, 56, 0.11);
  border-radius: 3px;
  margin-top: 2%;

  .title {
    width: 80%;
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 25px;
    color: $green_grade_5;
  }

  .sub_title {
    width: 80%;
    display: flex;

    span {
      font-size: 14px;
      margin: 8px 0 10px 0;
      color: rgb(27, 27, 27);
    }
  }

  .weather_container {
    width: 80%;
    height: 75%;
    background-color: white;
    margin-top: auto;
    margin-bottom: 12px;
    border-radius: 5px;
    @include flex_col;
    align-items: center;
    justify-content: center;
    font-size: 16px;

    .weather_line_one {
      @include flex_row;
      align-items: center;
      justify-content: space-between;
      width: 95%;
      height: 50%;

      .weather_icon {
        width: 50%;

        img {
          display: block;
          width: 100px;
          height: 100px;
          margin: 0 auto;
        }
      }

      .weather_info {
        @include flex_col;
        width: 50%;

        .temperature {
          font-weight: 300;
          font-size: 1.5em;
        }

        .explanation {
          font-weight: 300;
          font-size: 1.1em;
          margin-top: 7%;
          margin-bottom: 5px;
        }

        .location {
          font-weight: 300;
          font-size: 0.5em;
        }
      }
    }

    .weather_line_two {
      @include flex_row;
      align-items: center;
      justify-content: center;
      width: 95%;
      height: 30%;

      .temp_info {
        width: 31%;
        height: 60%;
        @include flex_col;
        align-items: center;
        justify-content: space-around;

        .temp_line {
          width: 80%;
          @include flex_row;
          justify-content: space-between;

          .temp_arrow {
            margin: auto 0;
            font-size: 0.8em;
            font-weight: 500;
          }
        }

        p {
          font-weight: 300;
          font-size: 0.8em;
        }
      }

      .humidity,
      .feels_like {
        width: 31%;
        height: 60%;
        border-left: 0.5px solid $gray;
        @include flex_col;
        align-items: center;
        justify-content: space-around;

        h1,
        p {
          font-weight: 300;
          font-size: 0.8em;
        }
      }
    }
  }
}

@include tablet {
  .weather {
    .weather_container {
      width: 90%;
      font-size: 13px;

      .weather_line_one {
        width: 110%;
      }
    }
  }
}

@include mobile {
  .weather {
    .weather_container {
      width: 94%;
      font-size: 13px;

      .weather_line_one {
        width: 110%;
      }
    }
  }
}

@include min {
  .weather {
    width: 100%;
    height: 280px;
    margin: 15px 0 80px 0;
  }
}
